import { createApp } from 'vue';
import App from './App.vue';
import router from './router';
// ÂëäËØâTypeScript routerÊ®°ÂùóÁöÑÁ±ªÂûã‰∏∫anyÔºåËß£ÂÜ≥Êâæ‰∏çÂà∞Â£∞ÊòéÊñá‰ª∂ÁöÑÈóÆÈ¢ò
/// <reference types="/Users/wujunfeng/GrassPillow.github.io/a-origin/src/router/index.ts" />
// ÂÖ®Â±ÄResizeObserverÈîôËØØÂ§ÑÁêÜ
const originalErrorHandler = window.onerror;
window.onerror = function (message, source, lineno, colno, error) {
    // Ê£ÄÊü•ÊòØÂê¶ÊòØResizeObserverÂæ™ÁéØÈîôËØØ
    const messageStr = typeof message === 'string' ? message : String(message || '');
    const errorMessage = error?.message || '';
    const errorStack = error?.stack || '';
    const isResizeObserverError = messageStr.includes('ResizeObserver loop') ||
        messageStr.includes('ResizeObserver loop completed with undelivered notifications') ||
        messageStr.includes('ResizeObserver loop limit exceeded') ||
        errorMessage.includes('ResizeObserver loop') ||
        errorMessage.includes('ResizeObserver loop completed with undelivered notifications') ||
        errorStack.includes('ResizeObserver');
    if (isResizeObserverError) {
        // ËøôÊòØ‰∏Ä‰∏™Â∑≤Áü•ÁöÑÊµèËßàÂô®ÈîôËØØÔºåÊàë‰ª¨ÂèØ‰ª•ÂÆâÂÖ®Âú∞ÂøΩÁï•ÂÆÉ
        // ÂÆåÂÖ®ÈòªÊ≠¢ÈîôËØØÊòæÁ§∫
        return true;
    }
    // ÂØπ‰∫éÂÖ∂‰ªñÈîôËØØÔºåË∞ÉÁî®ÂéüÂßãÂ§ÑÁêÜÂô®
    if (originalErrorHandler) {
        return originalErrorHandler.call(this, message, source, lineno, colno, error);
    }
    return false;
};
// ÂêåÊó∂Â§ÑÁêÜÊú™ÊçïËé∑ÁöÑPromiseÈîôËØØ
window.addEventListener('unhandledrejection', (e) => {
    const message = e.reason?.message || String(e.reason || '');
    const stack = e.reason?.stack || '';
    if (message.includes('ResizeObserver loop') ||
        message.includes('ResizeObserver loop completed with undelivered notifications') ||
        message.includes('ResizeObserver loop limit exceeded') ||
        stack.includes('ResizeObserver')) {
        e.preventDefault();
        e.stopImmediatePropagation();
        return true;
    }
    return false;
}, true);
// Â§ÑÁêÜÈîôËØØ‰∫ã‰ª∂Ôºà‰ΩøÁî®ÊçïËé∑Èò∂ÊÆµÔºâ
window.addEventListener('error', (e) => {
    const message = e.message || '';
    const stack = e.error?.stack || '';
    const isResizeObserverError = message.includes('ResizeObserver loop') ||
        message.includes('ResizeObserver loop completed with undelivered notifications') ||
        message.includes('ResizeObserver loop limit exceeded') ||
        stack.includes('ResizeObserver');
    if (isResizeObserverError) {
        e.preventDefault();
        e.stopImmediatePropagation();
        return true;
    }
    return false;
}, true);
// ÂàõÂª∫VueÂ∫îÁî®ÂÆû‰æã
const app = createApp(App);
// ‰ΩøÁî®Ë∑ØÁî±
app.use(router);
// ÊåÇËΩΩÂ∫îÁî®
app.mount('#app');
